.Recent-view-container{
    background-color: #f7f7f7;
    height: 1000px;
    

}
.Recent-view-container2{
    position: relative;
    top:400px;
}
.Recent-view-h1{
    font-size: 24px;
    line-height: 36px;
    color: #37404d;
    font-weight: 700;
    font-family: "Gentium Book Basic";
    text-align: center;
    }
.Recent-view-p{
    max-width: 769px;
    min-width: 350px;
    height: 80px;
    font-size: 16px;
    line-height: 24px;
    color: #37404d;
    font-weight: 400;
    font-family: "Poppins";
    text-align: center;
}
.Recent-view-container-row  img{
    height:238px;
    width: 270px;
    position:relative;
}
.owl-dots{
    display: none;
}

button.owl-prev{
    position:absolute;
    top:50%;
    left:-10%;
    background-color: black!important;
    width: 30px;
    color: #ffffff!important;
    font-size: 14px !important;
    border-radius: 0!important;

    
}
button.owl-prev span{
    opacity: 0!important;
} 
button.owl-next{
    position:absolute;
    top:50%;
    right:-10%;
    background-color: black!important;
    width: 30px;
    color: #ffffff!important;
    font-size: 14px !important;
    border-radius: 0!important;
}

@media (max-width: 769px) {
    button.owl-prev,button.owl-next{
        
        display: none !important;
    }
  }



