*{
    margin:0px;
    padding:0px;
    box-sizing: border-box;
}
.nav-section{
    background-color:#918ef4;
}
.navbar-brand{
    font-size: 24px;
    line-height: 24px;
    color: #ffffff !important;
    font-weight: 700;
    font-family: "Gentium Book Basic";
    text-decoration: none;
}
.top-nav-link {
    font-size: 16px;
    line-height: 24px !important;
    color: #ffffff !important;
    font-weight: 400;
    font-family: "Poppins";
    text-align: right;
}
.top-nav-link i{
    font-size: 24px;
    line-height: 30px;
    text-align: center;
}
.top-nav-link span{
    position: relative;
    top:0px;
    font-size: 12px;
    /* line-height: 30px; */
    font-weight: 600;
    text-align:center;
    width: 19px;
    height: 19px;
    border-radius: 10px;
    background-color: #ef7a85!important;
    border: 2px solid #9a97f1;
}