.pf-button{
    width: 154px;
    border-radius: 18px;
    background-color: #ffffff;
    border: 1px solid #37404d;
}
.pf-button a{
    font-size: 12px;
    line-height: 24px;
    color: #37404d;
    font-weight: 600;
    text-decoration: none;
    font-family: "Poppins";
}
.pf-button:hover a{
    color:#ffffff;
    
}

