
@media (min-width: 576px){
    .modal-dialog {
    max-width: 969px;
    margin: 1.75rem auto;
    height: 550px !important;
    }
}

.popupwindow h1{
    font-size: 20px;
    line-height: 20px;
    color: #37404d;
    font-weight: 700;
    font-family: "Gentium Book Basic";
}
.popupwindow h3{
    font-size: 14px;
    line-height: 30px;
    color: #37404d;
    font-weight: 600;
    font-family: "Poppins";
}
p.descriptions{
    
    height: 71px;
    font-size: 12px;
    line-height: 20px;
    color: #37404d;
    font-weight: 400;
    font-family: "Poppins";
}
.pricing{
    background-color: #ffffff;
    width:260px;
    padding:10px 20px;
}
.pricing p{
    font-size: 14px;
    line-height: 20px;
    color: #37404d;
    font-weight: 300;
    font-family: "Poppins";
    margin-bottom:3px !important;
}
.pricing span{
    font-size: 15px;
    line-height: 20px;
    color: #37404d;
    font-weight:bold;
    font-family: "Poppins";
}
img.small_photo{
    margin:10px 10px;
    text-align:center;
}
.quantity{
    font-size: 14px;
    color: #37404d;
    font-weight: 300;
    font-family: "Montserrat";
}

.btn_group{
    width: 133px;
    height: 36px;
    background-color: #ffffff;
    border: 1px solid #ededed;
}
.btn_minus,.btn_plus{
    width: 37px;
    height: 36px;
    text-align: center;
    background-color: #eeeeee;
    color: #303030;
    font-size: 20px;
    font-weight: bold;
    padding: 0px;
    
    
}
button,a{
    box-shadow: none!important;

}
.btn_plus{
    position:relative;
    right: -35px;
    /* margin-right: -20px!important; */
}
.btn_view{
    background-color: #ffffff;
    width:40px;
    text-align: center;
}
