.header{
    background-color: #918ef4;
}
.header-row{
    height: 622px;/* 682px */
}

.header-h1{
    font-size: 48px;
    line-height: 24px;
    color: #ffffff;
    font-weight: 700;
    font-family: "Gentium Book Basic";
    text-align: center;
}
.header-p{
    max-width: 770px;
    min-width: 350px;
    height: 106px;
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
    font-weight: 400;
    font-family: "Poppins";
    text-align: center;
}
.search-section{
    border-radius: 27px;
    position:relative;

}
form.search-section input[type=text]{
    width:430px;
    min-width:300px;
    height: 55px;
    border-radius: 27px;
    background-color: #ffffff;
}
::placeholder{
    font-size: 16px;
    color: #37404d;
    font-weight: 400;
    font-family: "Poppins";
}
.search-button{
    position:absolute;
    right:2%;
    top:10%;
    
}
.fa-search{
    font-size: 24px;
    color: #37404d;
    
}
@media (max-width: 576px) {
    form.search-section input[type=text]{
        width:330px;
  }
}