
.drop-button{
    width: 250px;
    height: 48px;
    border-radius: 24px;
    background-color: #ffffff;
    border: 1px solid #ededed;
     font-size: 16px; 
    line-height: 20px;
    color: #37404d;
    font-weight: 400;
    font-family: "Poppins";
    text-align: center;
}
.dropdown-item {
    width: 250px;
    text-align: center;
    color: #37404d!important;
    background-color: #ffffff!important;
}
.dropdown-item :hover{
    width: 250px;
    text-align: center;
    color: #ffffff!important;
    background-color:#918ef4 !important;
}

.dropdown-toggle::after {

    font-family:"Font Awesome 5 Free";
    content: "\f078";
    background: url("../image/angle-down-solid.svg");
    background-repeat: no-repeat;
    position: relative;
    left:5%;
    top:9%;
    color: #37404d;
    border: none;
}
.show .dropdown-toggle::after {
    font-family:"Font Awesome 5 Free";
    content: "\aaaa";
    background: url("../image/angle-up-solid.svg");
    background-repeat: no-repeat;
    position: relative;
    left:5%;
    top:9%;
    color: #37404d;
    border: none;  
}
